.screen {
    width: 100%;
    height: 75px;
    padding: 25px;
    border-radius: 10px;
    text-align: right;
    background-color: var(--screen-background);
    color: var(--screen-text);
    margin: 20px 0 ;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}